.navbar-wrapper {
  /*background-color: #40484b;*/
  background-color: #000000;
}

.navbar {
  background: transparent;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  position: sticky;
  top: 0;
  color: black;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
  background-color: black;
}

.navbar-logo a {
  font-family: "poppins";
  color: #eee6dd;
  justify-self: start;
  margin-left: 1.2em;
  margin-top: 15px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2.8rem;
  display: flex;
  font-weight: 800;
}

.navbar-logo a:hover {
  font-size: 2.1em;
}

a {
  text-decoration: none;
  transition: all 0.15s ease-in;
}

.spacer {
  flex: 0.97;
}

.navbar-items ul {
  list-style: none;
  text-align: right;
  margin-top: 16px;
  margin-left: 13px;
  margin-right: 20px;
  padding: 0;
  display: flex;
}

.navbar-items ul li .slash {
  display: inline;
  color: #fefefe;
  font-size: 1.1em;
  margin-left: 1.4em;
  margin-right: 1.4em;
}

.navbar-items a {
  text-decoration: none;

  color: #eee6dd;
  font-size: 24px;
}

.navbar-items a .not-available {
  text-decoration: line-through !important;
}

.navbar-items a:hover {
  border-bottom: 2px solid #f5f5f5;
  margin-left: 3px;
  margin-right: 3px;
}

